import React from 'react'

export function Wavy(text) {
  const charList = text.split('')
  return (
    <>
      {charList.map((char, index) => {
        return (
          <span key={char + index} style={{ animationDelay: `calc(0.05s * ${index})` }}>
            {char}
          </span>
        )
      })}
    </>
  )
}
