import React, { useEffect } from 'react'
import LocomotiveScroll from 'locomotive-scroll'
import PropTypes from 'prop-types'

import '../styles/layout.css'
import '../styles/main.scss'
import { Toolbar } from './toolbar'
import { Footer } from './footer'

export const Layout = ({ children }) => {
  useEffect(() => {
    if (typeof window !== 'undefined') {
      let cursor
      import('../animations/assists/cursor').then(({ Cursor }) => {
        cursor = new Cursor(document.querySelector('.cursor'))
      })
      let locomotiveScroll = new LocomotiveScroll({
        el: document.querySelector('main'),
        smooth: true,
        smoothMobile: true,
        getDirection: true,
        touchMultiplier: 2.5,
        lerp: 0.08
      })
      locomotiveScroll.update()

      // Exposing to the global scope for ease of use.
      window.scroll = locomotiveScroll

      locomotiveScroll.on('scroll', (func) => {
        document.documentElement.setAttribute('data-direction', func.direction)
      })

      return () => {
        if (locomotiveScroll) locomotiveScroll.destroy()
      }
    }
  }, [])

  return (
    <>
      <Toolbar />
      <main>{children}</main>
      <Footer />
      <svg className="cursor" width="80" height="80" viewBox="0 0 80 80">
        <circle className="cursor__inner" cx="40" cy="40" r="20" />
      </svg>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired
}
